import { Axios } from 'axios'
import {
  Campaign,
  CampaignData,
  CampaignsSearchQuery,
  CampaignsClientModel,
  CampaignRoutes,
  CampaignPoints,
  CampaignRankingQuery,
  CampaignRanking,
} from '../campaign.model'
import { ApiEntity, uriFy } from '../../core'

export class CampaignsClient extends ApiEntity implements CampaignsClientModel {
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Create a campaign
   * @param body - the campaign data
   * @returns the promise for create a new campaign
   */
  public create(body: CampaignData) {
    return this._create<Campaign>(CampaignRoutes.v1Create, body)
  }

  /**
   * Search epochs by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for search epochs
   */
  public search(searchParams?: CampaignsSearchQuery) {
    return this._search<Campaign>(CampaignRoutes.v1Search, searchParams)
  }

  /**
   * Search all epoch by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: CampaignsSearchQuery) {
    return this._searchAll<Campaign>(CampaignRoutes.v1Search, searchParams)
  }

  /**
   * List epochs by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for list epochs
   */
  public list(searchParams?: CampaignsSearchQuery) {
    return this._list<Campaign>(CampaignRoutes.v1Search, searchParams)
  }

  /**
   * List all epochs by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for list epochs
   */
  public listAll(searchParams?: CampaignsSearchQuery) {
    return this._listAll<Campaign>(CampaignRoutes.v1Search, searchParams)
  }

  /**
   * Find an epoch by params
   * @param searchParams - the search params
   * @returns the promise for find an epoch
   */
  public findOne(searchParams?: CampaignsSearchQuery) {
    return this._findOne<Campaign>(CampaignRoutes.v1Search, searchParams)
  }

  /**
   * Read a campaign by params
   * @param searchParams - the search params
   * @returns the promise for read a campaign
   */
  public readOne(searchParams: CampaignsSearchQuery) {
    return this._readOne<Campaign>(CampaignRoutes.v1Search, searchParams)
  }

  /**
   * Get campaign points for a wallet address
   * @param campaignId - the campaign ID
   * @param walletAddress - the wallet address
   * @returns the points of a campaign for a wallet
   */
  public points(campaignId: string, walletAddress: string) {
    return this.axios
      .request<CampaignPoints>({
        url: CampaignRoutes.v1Points.replace(':campaignId', campaignId),
        method: 'GET',
        params: new URLSearchParams(uriFy({ walletAddress })),
      })
      .then((response) => response.data)
  }

  /**
   * Get campaign ranking
   * @param campaignId - the campaign ID
   * @param query - the campaign ranking query
   * @returns the ranking of a campaign
   */
  public ranking(campaignId: string, query?: CampaignRankingQuery) {
    return this.axios
      .request<CampaignRanking>({
        url: CampaignRoutes.v1Ranking.replace(':campaignId', campaignId),
        method: 'GET',
        params: new URLSearchParams(uriFy(query)),
      })
      .then((response) => response.data)
  }
}
